<template>
  <div>
    <van-sticky>
      <van-nav-bar title="个人信息" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <div class="userWrap">
      <van-cell-group>
        <van-field label="头像">
          <div slot="right-icon">
            <van-uploader :after-read="upImg" accept="image/*">
              <van-image
                :src="userInfo.wechat_img"
                class="userImg"
                round
                v-if="userInfo.wechat_img"
              >
              </van-image>
            </van-uploader>
          </div>
        </van-field>
        <van-field
          v-model="userInfo.nick_name"
          label="昵称"
          input-align="right"
          placeholder="请输入昵称"
        />
        <van-field
          label="性别"
          input-align="right"
          placeholder="请输入性别"
          v-model="userInfo.sex"
        >
        </van-field>
        <van-field
          v-model="userInfo.pid_member.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2')"
          label="推荐人"
          input-align="right"
          placeholder="请输入推荐人"
          disabled
        />
      </van-cell-group>
      <button class="submitBtn" @click="updateUser">确认信息</button>
    </div>
  </div>
</template>

<script>
import { userCenter, uploadImg, updateUser } from "@/api/personal";
export default {
  data() {
    return {
      userInfo: {
        pid_member: "",
        nick_name: "",
        wechat_img: "",
        sex: ""
      },
      img: ""
    };
  },
  created() {
    this.getUser();
  },
  methods: {
    getUser() {
      userCenter().then(res => {
        this.userInfo = res;
        this.userInfo.sex = this.userInfo.sex == 1 ? "男" : "女";
      });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    upImg(file) {
      this.image = file.content.substring(file.content.indexOf(",") + 1);
      uploadImg(this.image, "").then(resp => {
        this.userInfo.wechat_img = resp.img;
      });
    },
    // 提交
    updateUser() {
      this.userInfo.sex = this.userInfo.sex == "男" ? 1 : 2;
      updateUser(this.userInfo).then(res => {
        setTimeout(() => {
          this.$toast("修改成功!");
        }, 200);
        this.$router.go(-1);
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.van-cell {
  padding: 21px 16px 19px;
}
.userImg {
  width: 39px;
  height: 39px;
}
.submitBtn {
  display: block;
  width: 345px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  font-size: 18px;
  border-radius: 20px;
  background: #28A6FF;
  margin: 66px auto;
  border: 0 none;
}
</style>
